import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ConstantCommon } from '../../psServices/models/common-constant';
import { CommonUtils } from '../../psServices/models/common-utils';
import { IOptionsPsActionImage, IOptionsPsComplexPreview, IOptionsPsContainerPanel, IOptionsPsLabel, IPsTemplatePreviewComponent } from '../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { PsCommonService } from '../../psServices/ps-common/ps-common.service';
import { PsApplicationSettings } from '../../psServices/models/ps-app-settings';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';

@Component({
  selector: 'ps-complex-preview',
  templateUrl: './ps-complex-preview.template.html',
  styleUrls: ['./ps-complex-preview.template.scss'],
})
export class PsComplexPreview {

  public allData;
  private group: FormGroup;
  public isMultiple = false;
  previewMode: boolean;
  enableTranslation: boolean;

  columnHeaderPreview = [];
  edittedRows = [];
  PreviewWasLoaded:boolean = false;


  // TP # 888440
  colStr: string;
  // TP # 888440
  /*  @Input('group') set _group(grp: Array<FormGroup>) {
     this.group = grp;
     for (const group of this.group) {
       this.common.previewMode(group);
     }
     setTimeout(() => {
       this.selectData();
     }, 0);
   } */

  previewPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: this.common.translate('summary_key'),
    iconName: 'keypad',
    expanded: true
  }

  showGridPreview: boolean;
  gridData = {};
  multiView: boolean = false;
  spinner = false;
  actionImageOptions: IOptionsPsActionImage = {
    imageName: CommonUtils.getCssVariableValue('--ps-loader-image-name')
  };

  sysParamScreenDispList = this.common.initialScreenDisplayParams.get(PsCommonSettings.oper_ID);
  custKeys = Array.from(this.sysParamScreenDispList.keys());
  get previewData() {
    return this.allData;
  }
  @Input('data') set _data(param) {
    if (param) {
      this.enableTranslation = true; // Added for Approval List Case where I need to translate the Keys in details screen 821619
      this.allData = param;
    }
  }

  @Input('options') set options(opt: IOptionsPsComplexPreview) {
    this.group = opt.group;
    if (this.custKeys.findIndex(v => v.includes("_preview")) > -1) {
      if (this.sysParamScreenDispList.get(this.custKeys[this.custKeys.findIndex(v => v.includes("_preview"))]).IS_VISIBLE === 1) {
        this.handlePreviewData();
      }
    } else { this.handlePreviewData(); }

  }
  constructor(private common: PsCommonService) {
  }


  selectData() {
    this.spinner = true;
    this.allData = new Array<IPsTemplatePreviewComponent>();
    const delimiter = ConstantCommon.PREVIEW_COMPONENT_DELIMITER_HEX;
    const page = document.querySelectorAll(".operId_" + PsCommonSettings.oper_ID);
    const elements: NodeListOf<Element> = page[page.length - 1].querySelectorAll('[psPreviewEngine]');
    if (elements && elements.length > 0) {
      for (let i = 0; i < elements.length; i++) {
        const component: Element = elements[i];

        const children: Array<Element> = Array.prototype.slice.call(component.children);
        const psPreviewLabel = children.find(o => o.hasAttribute('psPreviewLabel'));
        const psPreviewHTML = children.find(o => o.hasAttribute('psPreviewHTML'));
        const psPreviewGrid = children.find(o => o.hasAttribute('psPreviewGrid'));
        const psPreviewMultiTemplate = children.find(o => o.hasAttribute('psPreviewMultiTemplate'));
        if (psPreviewGrid !== undefined) {
          this.showGridPreview = true;
        }
        if ((psPreviewMultiTemplate && psPreviewMultiTemplate.textContent
          && !CommonUtils.isEmptyString(psPreviewMultiTemplate.textContent.replace(/ /g, '')))) {
          this.allData.push({ key: psPreviewMultiTemplate ? psPreviewMultiTemplate.textContent : '', value: false });
          this.multiView = true;
        }
        if ((psPreviewLabel && psPreviewLabel.textContent
          && !CommonUtils.isEmptyString(psPreviewLabel.textContent.replace(/ /g, ''))) || psPreviewHTML) {
          //TP#1331866 - pass the gridViewPreview boolean to the allData array for the needed records only
          this.allData.push({ key: psPreviewLabel ? psPreviewLabel.textContent : '', value: psPreviewHTML ? psPreviewHTML.outerHTML : '', innerHTML: psPreviewHTML ? true : false, showGridPreview: this.showGridPreview });
        } else {
          const text = component.textContent;
          if (text.indexOf(delimiter) == -1) {
            continue;
          }
          let splited, key, value;

          splited = text.trim().split(delimiter);
          key = splited[0];
          value = splited[1];
          this.showGridPreview = false;

          if (value == undefined && /\d/.test(splited[0])) {
            value = splited[0].replace(/[^0-9]/g, '');
          }
          if (!key || !value) {
            continue;
          }
          //TP#1331866 - pass the gridViewPreview boolean to the allData array for the needed records only
          this.allData.push({ key: key.trim(), value: value.trim(), showGridPreview: this.showGridPreview });
        }
      }
      // TP # 888440
      if (this.allData.length == 1) {
        this.colStr = 'col-12';
      } else {
        this.colStr = 'col-6';
      }
      // TP # 888440
    }
    this.spinner = false;
  }

  ngOnDestroy() {
    this.common.editMode(this.group);
  }
  handlePreviewData() {
    this.common.previewMode(this.group);
    setTimeout(() => {
      this.selectData();
    }, 0);
  }
}
